<template>
    <div class="PhoneSetting">
      <HomePageTop :TopTitle="TopTotle" class="bg_fff"> </HomePageTop>
      <div>
      <div class="PhoneNumber">
        <p> 当前手机号（可用于登录）</p>
        <span>{{phonenumber}}</span>
      </div>
      <div class="codeSending">
        <span>短信验证码</span>
        <p> <input type="text" placeholder="请输入验证码" @input="inputs" v-model="values" maxlength="4"></p>
        <div @click="countDown">{{count}}</div>
      </div>
      <div class="public-btn mt60  mr30 ml30" :class="SubmitClassbox==true?'public-btn':'public-btnNo'" @click="SubmitClassClick">
        提交
      </div>
      </div>

    </div>

</template>

<script>
    import Vue from "vue"
    import HomePageTop from "../../common/HomePageTop";

    export default {
        name: "PhoneSetting",
        components: {HomePageTop},
        data() {
            return {
              //像子组件传值
              TopTotle:{name:"手机号",left:true},
              phonenumber:"17637408019",
              //  验证码
              verifyModelTrue:false,
              verifyModelText:"请输入",
              //  倒计时的时间数值
              count:"获取验证码",
              //  验证码model
              verifyModel:"",
              //提交按钮颜色
              SubmitClassbox:false,
              values:"",
            }
        },
        methods: {
          countDown(){
            if(this.count=="获取验证码"||this.count=="验证码发完了"){
              let lastTime=59;
              let timer = setInterval(() => {
                if(lastTime>0){
                  $(".PhoneSetting .codeSending div").css({
                    "color":"#666"
                  })
                  this.count="倒计时"+lastTime+"s";
                  lastTime--;
                }else{
                  window.clearInterval(timer);
                  this.count="验证码发完了"
                  $(".PhoneSetting .codeSending div").css({
                    "color":"#f90"
                  })
                }
              },1000)

            }else{
              return;
            }
          },
          inputs(){
            console.log(this.values);
            if(this.values.length>=4){
              this.SubmitClassbox=true
            }
          },
          SubmitClassClick(){
            if(this.values.length>=4){
              this.$router.push({
                path:"PhoneSetting2"
              })

            }

          },
        },
        created() {
          let reg=/^(\d{3})\d*(\d{4})$/;
          this.phonenumber=this.phonenumber.replace(reg,'$1****$2')
          console.log(this.phonenumber);
        }
    }

</script>

<style  lang="less">
  .PhoneSetting{

    .PhoneNumber{
      background: #ffffff;
      border-top: 1px solid #D8D8D8;
      height: 3.12rem;
      width: 100%;
      padding-top: 1rem;
      text-align: center;
      p{
        font-size: .28rem;
        color: #666;
      }
      span{
        font-size: .42rem;
        color: #333;
        margin-top: .1rem;
        display: block;
      }
    }
    .codeSending{
      margin-top: .24rem;
      padding: 0 .3rem;
      height: 1.2rem;
      background: #fff;
      span{
        float: left;font-size: .34rem;color: #666;line-height: 1.2rem;
      }
      p{
        display: inline-block;
        float: left;
        padding: 0.444rem 0;
        height: 1.2rem;
        width: 3.5rem;
        padding-left: .2rem;
        input{
          width: 100%;
          border: none;
        font-size: .28rem;
          border-right: 1px solid #D1D3D8;

        }
      }

      div{
        padding-left: .24rem;
        display: inline-block;
        vertical-align: middle;
        font-size: 0.28rem;
        float: right;line-height: 1.2rem;
        color: #f90;
      }
    }

  }

</style>



