        <div class="selectAreaCode">
          <div class="AreaCodeShow">
            <span>+86</span>
          </div>

          <input  class="OnInput" type="text" placeholder="请输入" autocomplete="off" oninput="value=value.replace(/[^\d]/g,'')" maxlength="11" v-model="inpurValue" @input="Oninput">
