            }
        },

        methods: {
          onSearch() {},


          //监听输入框不能让按钮变色
          Oninput(){this.inpurValue.length>=11?this.SubmitClassbox=true:this.SubmitClassbox=false},
          SubmitClassClick(){
            console.log(this.$router);
            let myreg=/^[1][3,4,5,7,8][0-9]{9}$/;
            let _this=this

            !myreg.test(this.inpurValue)==true?(this.inpurValue==""?console.log("为空"):console.log("格式不对")):Toast({ message: '顶部展示',onClose(){_this.$router.push({path:"SafetySetting"})}})

            //
            // if(!myreg.test(this.inpurValue)){
            //   if(this.inpurValue==""){
            //     console.log("为空");
            //   }else{
            //     console.log("格式不对");
            //   }
            // }else{
            //   Toast({
            //     message: '顶部展示',
            //     onClose(){
            //       console.log(_this.$router);
            //       _this.$router.push({path:"SafetySetting"})
            //     },
            //   });
            //
            // }
          },

        },
        created() {

        },

    }

</script>

