  .PhoneSetting2 {height: 100%;  background: #fff !important;}
  .selectAreaCode{
    width: 6.9rem;
    height: .84rem;
    border: 1px solid #f90;
    margin: 0 auto;
    border-radius: .04rem;
    text-align: left;
    padding-left: .24rem;
  }
  .AreaCodeShow{
    width: .8rem;
    display: inline-block;
    vertical-align: middle;
    line-height: .82rem;

  }
  .AreaCodeShow span{
    font-size: .28rem;
  }
  .AreaCodeShow img{
    width: .16rem;
  }
.phoneTop{
  padding-top: 1.6rem;
  text-align: center;
}
  .OnInput{
    width: 4.42rem;
    /*height: .84rem;*/
    border: none;
    vertical-align: middle;
    font-size: .28rem;
  }
  .atnight{
    background: #fff;
  }

  .main {
    margin: 0.1rem 1.173333rem;

  .list {
    height: 2.4rem;
    line-height: 2.4rem;
    justify-content: space-between;
    font-size: 0.746666rem;
    border-bottom: 1px solid #EBEBEB;
  }
  }
</style>



